import { useState } from "react";
import { criarCliente } from "../services/api"; // Ajuste o caminho conforme necessário

const CriarCliente = () => {
  const [form, setForm] = useState({
    nome: "",
    fantasia: "",
    documento: "",
    endereco: "",
  });

  const handleChange = (e) => {
    setForm({ ...form, [e.target.name]: e.target.value });
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    try {
      const resposta = await criarCliente(form);
      console.log("Cliente criado com sucesso:", resposta);
      alert("Cliente criado com sucesso!");
    } catch (error) {
      alert("Erro ao criar cliente. Veja o console.");
    }
  };

  return (
    <form onSubmit={handleSubmit}>
      <input type="text" name="nome" placeholder="Nome" onChange={handleChange} />
      <input type="text" name="fantasia" placeholder="Fantasia" onChange={handleChange} />
      <input type="text" name="documento" placeholder="Documento" onChange={handleChange} />
      <input type="text" name="endereco" placeholder="Endereço" onChange={handleChange} />
      <button type="submit">Criar Cliente</button>
    </form>
  );
};

export default CriarCliente;
